@font-face {
  font-family: lato;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/latoregular.woff2) format("woff2"), url(../fonts/latoregular.woff) format("woff"), url(../fonts/latoregular.ttf) format("truetype");
}
@font-face {
  font-family: lato;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/latobold.woff2) format("woff2"), url(../fonts/latobold.woff) format("woff"), url(../fonts/latobold.ttf) format("truetype");
}
@font-face {
  font-family: lato;
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/latoblack.woff2) format("woff2"), url(../fonts/latoblack.woff) format("woff"), url(../fonts/latoblack.ttf) format("truetype");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  width: 88%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    width: 92%;
  }
}
.title {
  font: inherit;
  font-size: 28px;
  font-weight: 900;
  margin: 0;
  line-height: 1;
  text-transform: capitalize;
}
.subtitle {
  margin: 0;
  color: #a1a1a1;
  text-transform: uppercase;
  font: inherit;
}
.button {
  display: block;
  border: 1px solid #a1a1a1;
  width: 139px;
  border-radius: 10px;
  padding: 10px 0;
  box-sizing: border-box;
  text-decoration: none;
  font: inherit;
  color: #fff;
  text-align: center;
}
.button:hover,
.buttonfocus {
  border-color: #fff;
  color: #a1a1a1;
}
.page {
  height: 100%;
  font-family: "lato", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.3;
  font-weight: 400;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  max-width: 1440px;
  min-height: 100%;
  margin: 0 auto;
}
.page-header {
  background-color: #583fbe;
  padding: 20px 0;
  box-sizing: border-box;
}
.wrapper--header {
  display: grid;
  grid-template-columns: 70px 1fr min-content;
  align-items: center;
}
@media (min-width: 768px) {
  .wrapper--header {
    grid-template-columns: 100px 1fr min-content;
    column-gap: 10px;
  }
}
.logo {
  width: 100px;
  height: 50px;
  outline: none;
}
.logo:hover,
.logo:focus {
  opacity: 0.6;
}
.page-header__social {
  justify-self: end;
  display: flex;
  flex-wrap: wrap;
}
.main-nav__toggle {
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  width: 30px;
  height: 32px;
}
.main-nav__toggle:hover,
.main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 3px;
  background-color: #fff;
  top: 0;
  left: 0;
  box-shadow: 0 12px 0 0 #fff, 0 24px 0 0 #fff;
}
.main-nav--closed {
  display: none;
}
.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.main-nav__item a {
  color: #fff;
  text-decoration: none;
}
.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.social__img {
  stroke: #fff;
  outline: none;
  width: 50px;
  height: 50px;
}
.social__img:hover,
.social__img:focus {
  cursor: pointer;
  opacity: 0.6;
}
.social__img--contacts {
  width: 90px;
  height: 90px;
}
.promo {
  color: #a1a1a1;
  background-color: #583fbe;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}
.promo__slogan {
  font: inherit;
  font-size: 36px;
  font-weight: 900;
  color: #fff;
  margin: 0;
  padding: 0;
  text-transform: capitalize;
}
.promo__desc {
  margin: 20px 0;
}
.promo__nav {
  display: flex;
}
.button--aboute {
  color: #583fbe;
  background-color: #F8F8AE;
  border: none;
  margin: 0 2% 0 0;
}
.button--aboute:hover,
.button--aboutefocus {
  color: #fff;
  background-color: transparent;
  border: 1px solid #F8F8AE;
  box-sizing: border-box;
}
.promo__photo {
  display: block;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  margin: 0 auto;
}
.expertise {
  padding: 40px 0;
  box-sizing: border-box;
}
.expertise__title {
  margin: 10px 0;
}
.expertise__desc {
  margin: 0 0 30px 0;
  color: #a1a1a1;
}
.expertise__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.experience-card {
  background-color: #F8F8F8;
  padding: 20px;
  box-sizing: border-box;
  margin: 0 0 20px 0;
  border-radius: 10px;
}
.experience-card:last-child {
  margin: 0;
}
.experience-card__title {
  margin: 0;
  font-weight: 900;
}
.experience-card__desc {
  color: #a1a1a1;
}
.skill {
  background-color: #583fbe;
  padding: 2% 0;
  border-radius: 30px;
  box-sizing: border-box;
}
.skill__info {
  background-color: #F8F8F8;
  border-radius: 30px;
  width: 96%;
  margin: 0 auto;
  padding: 40px 0;
  box-sizing: border-box;
}
.skill__title {
  margin: 10px 0;
}
.skill__desc {
  margin: 0;
}
.skill__list {
  padding: 0;
  margin: 30px 0 0 0;
  list-style: none;
}
.skill__card {
  background-color: #fff;
  text-align: center;
  border-radius: 10px;
}
.stat__list {
  padding: 0;
  margin: 20px 0 0 0;
  list-style: none;
}
.stat__item {
  text-align: center;
  width: 150px;
  margin: 0 auto;
}
.stat__number {
  color: #fff;
  font-weight: 900;
}
.stat__desc {
  color: #a1a1a1;
}
.works {
  padding: 40px 0 0 0;
  text-align: center;
  margin: 0 0 40px 0;
}
.works__title {
  margin: 20px 0 40px 0;
}
.works__list {
  margin: 0 0 40px 0  ;
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  row-gap: 10px;
}
@media (min-width: 768px) {
  .works__list {
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 10px;
  }
}
.works__item {
  justify-self: center;
}
.button--works {
  margin: 0 auto;
  color: #583fbe;
}
.project {
  border-radius: 15px;
  border: 1px solid transparent;
  background-color: #583fbe;
  max-width: 360px;
  position: relative;
  box-sizing: border-box;
}
.project:hover .project__img,
.project:focus .project__img {
  filter: brightness(33%);
}
.project:hover .project__title,
.project:focus .project__title {
  color: #000;
}
.project:hover,
.project:focus {
  background-color: #fff;
  border: 1px solid #000;
  box-sizing: border-box;
}
.project:hover .project__link,
.project:focus .project__link {
  display: block;
  position: absolute;
  top: 23%;
  left: 35%;
}
.project:hover .project__view,
.project:focus .project__view {
  display: block;
  position: absolute;
  top: 23%;
  right: 35%;
}
.project__img {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.project__info {
  padding: 20px;
  box-sizing: border-box;
  text-align: left;
}
.project__title {
  margin: 0 0 10px 0;
  color: #fff;
  text-transform: capitalize;
  font: inherit;
  font-weight: 700;
  font-size: 18px;
}
.project__desc {
  margin: 0;
  color: #a1a1a1;
}
.project__link,
.project__view {
  display: none;
  fill: #fff;
}
.project__link:hover,
.project__view:hover,
.project__link:focus,
.project__view:focus {
  opacity: 0.6;
}
.education {
  display: none;
}
.reviews {
  display: none;
}
.blog {
  display: none;
}
.contacts {
  background-color: #583fbe;
  padding: 2% 0;
  border-radius: 30px;
  box-sizing: border-box;
}
.contacts__info {
  background-color: #F8F8F8;
  border-radius: 30px;
  width: 96%;
  margin: 0 auto 50px auto;
  padding: 40px 0;
  box-sizing: border-box;
}
.contacts__title {
  margin: 10px 0;
}
.contacts__desc {
  margin: 0 0 40px 0;
  color: #a1a1a1;
}
.contacts__form {
  padding: 20px;
  box-sizing: border-box;
}
.communication {
  background-color: #fff;
  border-radius: 15px;
}
.communication__title {
  font: inherit;
  font-weight: 700;
  text-transform: capitalize;
  font-size: 18px;
}
.communication__name,
.communication__email,
.communication__text {
  outline: none;
  border-radius: 15px;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid;
}
.communication__name:focus,
.communication__email:focus,
.communication__text:focus {
  border-color: #583fbe;
}
.communication__text {
  resize: none;
  margin: 0 0 10px 0;
}
.button--communication {
  width: 100%;
  background-color: #583fbe;
}
.contacts-social__title {
  color: #fff;
}
.contacts-social__desc {
  color: #a1a1a1;
}
.contacts-social__list {
  display: flex;
  justify-content: space-between;
}
.page-footer {
  position: relative;
  z-index: -1;
  margin: -30px 0 0 0;
}
.footer-top {
  padding: 70px 0 40px 0;
  background-color: #333;
}
.footer-top__desc {
  color: #a1a1a1;
  margin: 16px 0 0 0;
}
.footer-top__nav {
  margin: 60px 0;
  line-height: 1.6;
}
.footer-top__email {
  color: #fff;
  font-weight: 700;
  margin: 16px 0 0 0;
  text-decoration: none;
}
.footer-bottom {
  background-color: #292929;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer-bottom__copyright {
  margin: 0;
  color: #a1a1a1;
}
